





















































import { Component, Vue, Ref } from "vue-property-decorator";
import vueFilePond, { VueFilePondComponent } from "vue-filepond";
import { PDFDocument, PageSizes, PDFImage } from "pdf-lib";
import download from "downloadjs";

import "filepond/dist/filepond.min.css";
import "filepond-plugin-image-preview/dist/filepond-plugin-image-preview.min.css";

import FilePondPluginFileValidateType from "filepond-plugin-file-validate-type";
import FilePondPluginImagePreview from "filepond-plugin-image-preview";

import loadImage from "blueimp-load-image";

const FilePond = vueFilePond(
  FilePondPluginFileValidateType,
  FilePondPluginImagePreview
);

@Component({
  components: {
    FilePond
  }
})
export default class Home extends Vue {
  private imageMode: [number, number] | null = null;
  private imageModeOptions = [
    { value: null, text: "Original Size" },
    { value: PageSizes.Letter, text: "Letter" },
    { value: PageSizes.A2, text: "A2" },
    { value: PageSizes.A3, text: "A3" },
    { value: PageSizes.A4, text: "A4" },
    { value: PageSizes.A5, text: "A5" },
    { value: PageSizes.A6, text: "A6" },
    { value: PageSizes.B3, text: "B3" },
    { value: PageSizes.B4, text: "B4" },
    { value: PageSizes.B5, text: "B5" },
    { value: PageSizes.B6, text: "B6" },
    { value: PageSizes.B7, text: "B7" },
    { value: PageSizes.B8, text: "B8" },
    { value: PageSizes.Legal, text: "Legal" }
  ];
  private interleave = false;
  private progressBar = {
    hidden: true,
    value: 10,
    max: 20,
    variant: "primary",
    label: "",
    animated: true
  };
  @Ref() readonly pond!: VueFilePondComponent;

  public merge(): void {
    this.mergeAsync();
  }

  private showError(msg: string): void {
    this.progressBar.variant = "danger";
    this.progressBar.max = 1;
    this.progressBar.value = 1;
    this.progressBar.label = msg;
    this.progressBar.hidden = false;
    this.progressBar.animated = false;
  }

  private resetProgressBar(max: number): void {
    this.progressBar.variant = "primary";
    this.progressBar.max = max;
    this.progressBar.value = 0;
    this.progressBar.label = "";
    this.progressBar.hidden = false;
    this.progressBar.animated = true;
  }

  private async download(doc: PDFDocument): Promise<void> {
    doc.setAuthor("https://pdf.yanchen.ca/");
    doc.setCreator("https://pdf.yanchen.ca/");
    doc.setProducer("https://pdf.yanchen.ca/");

    const mergedPdfFile = await doc.save();

    download(mergedPdfFile, "merged.pdf", "application/pdf");

    this.progressBar.hidden = true;
  }

  private async getPDF(file: any): Promise<PDFDocument> {
    const bytes = await new Response(file.file).arrayBuffer();
    return await PDFDocument.load(bytes);
  }

  public async mergeAsync(): Promise<void> {
    const files = this.pond.getFiles();
    const mergedPdf = await PDFDocument.create();

    if (this.interleave) {
      if (
        files.length !== 2 ||
        files[0].fileType !== "application/pdf" ||
        files[1].fileType !== "application/pdf"
      ) {
        this.showError("interleave mode only support 2 PDFs.");
        return;
      }

      const pdf1 = await this.getPDF(files[0]);
      const pdf2 = await this.getPDF(files[1]);
      if (pdf1.getPageCount() !== pdf2.getPageCount()) {
        this.showError(
          "interleave mode only support 2 PDFs with same number of pages."
        );
        return;
      }

      this.resetProgressBar(pdf1.getPageCount());
      for (let i = 0; i < pdf1.getPageCount(); i++) {
        const copiedPages1 = await mergedPdf.copyPages(pdf1, [i]);
        copiedPages1.forEach(page => {
          mergedPdf.addPage(page);
        });

        const copiedPages2 = await mergedPdf.copyPages(pdf2, [
          pdf1.getPageCount() - 1 - i
        ]);
        copiedPages2.forEach(page => {
          mergedPdf.addPage(page);
        });
        this.progressBar.value += 1;
      }
      await this.download(mergedPdf);
    } else {
      this.resetProgressBar(files.length);
      for (const file of files) {
        switch (file.fileType) {
          case "image/jpeg": {
            const jpegData = await this.getCanvas(file.file);
            const canvas = await this.getCanvasBlob(jpegData);
            if (canvas == null) {
              break;
            }
            const bytes = await new Response(canvas).arrayBuffer();

            const jpgImage = await mergedPdf.embedJpg(bytes);
            const pageSize = this.calculateImageSize(jpgImage);
            const page = mergedPdf.addPage(pageSize);

            page.drawImage(jpgImage, {
              x: 0,
              y: 0,
              width: page.getWidth(),
              height: page.getHeight()
            });
            break;
          }
          case "image/png": {
            const bytes = await new Response(file.file).arrayBuffer();

            const pngImage = await mergedPdf.embedPng(bytes);
            const pageSize = this.calculateImageSize(pngImage);

            const page = mergedPdf.addPage(pageSize);
            page.drawImage(pngImage, {
              x: 0,
              y: 0,
              width: page.getWidth(),
              height: page.getHeight()
            });
            break;
          }
          case "application/pdf": {
            const bytes = await new Response(file.file).arrayBuffer();

            const pdf = await PDFDocument.load(bytes);
            const copiedPages = await mergedPdf.copyPages(
              pdf,
              pdf.getPageIndices()
            );
            copiedPages.forEach(page => {
              mergedPdf.addPage(page);
            });
            break;
          }
        }
        this.progressBar.value += 1;
      }
      await this.download(mergedPdf);
    }
  }

  private async getCanvasBlob(canvas: HTMLCanvasElement): Promise<Blob | null> {
    return new Promise(function(resolve) {
      canvas.toBlob(function(blob: Blob | null) {
        resolve(blob);
      }, "image/jpeg");
    });
  }

  private async getCanvas(blob: Blob): Promise<HTMLCanvasElement> {
    return new Promise(function(resolve) {
      loadImage(
        blob,
        (image: Event | HTMLCanvasElement | HTMLImageElement): void => {
          resolve(image as HTMLCanvasElement);
        },
        {
          canvas: true
        }
      );
    });
  }

  private calculateImageSize(pdfImage: PDFImage): [number, number] | undefined {
    let pageSize: [number, number] | undefined = undefined;
    if (this.imageMode != null) {
      pageSize = [this.imageMode[0], this.imageMode[1]];
    }
    const pdfImageSize = [pdfImage.width, pdfImage.height];
    if (
      pageSize === undefined ||
      (pageSize[0] >= pdfImage.width && pageSize[1] >= pdfImage.height)
    ) {
      pageSize = [pdfImageSize[0], pdfImageSize[1]];
    } else {
      if (
        this.xor(pageSize[0] > pageSize[1], pdfImageSize[0] > pdfImageSize[1])
      ) {
        pageSize = [pageSize[1], pageSize[0]];
      }
      // keep aspect ratio
      const expectedRatio = pdfImageSize[0] / pdfImageSize[1];
      const actualRatio = pageSize[0] / pageSize[1];
      if (actualRatio > expectedRatio) {
        pageSize[0] = pageSize[1] * expectedRatio;
      } else {
        pageSize[1] = pageSize[0] / expectedRatio;
      }
    }
    return pageSize;
  }

  private xor(left: boolean, right: boolean) {
    return (left && !right) || (!left && right);
  }
}
